.main-container {
    margin-left: auto;
    margin-right: auto;
    max-width: 1200px
}

@media (min-width:700px) and (max-width:1320px) {
    .main-container {
        padding-left: 7%
    }
}

.boc__section {
    padding: 10px;
}



.row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-bottom: 10px;
}

.post-title-wrapper {
    border-bottom-color: #c8cccc;
    border-bottom-style: solid;
    border-bottom-width: 1px
}



.best-of-content {
    margin: 0 auto;
    overflow: hidden;
    position: relative
}

@media (min-width:768px) {
    .best-of-content {
        max-width: 300px
    }
}

@media(max-width:1022px) {
    #header-root-vue {
        margin-top: 60px !important;
    }
}

.boc__section {
    background-color: #fff;
    font-family: Georgia, Cambria, Times New Roman, Times, serif;
    font-size: 16px;
    margin-top: 50px;
}

@media (min-width:768px) {
    .boc__section {
        margin: 35px 0
    }
}

#default-page-vue-root .right-rail .right-rail-content .best-of-content .boc__section .boc__sub-section {
    margin: 0 0 12px;
    position: relative
}

@media (min-width:768px) {
    .best-of-content .boc__section .boc__sub-section {
        margin: 16px 0 32px
    }
}

@media (max-width:767px) {
    .best-of-content .boc__section .boc__sub-section:last-child {
        margin-bottom: 0
    }
}

.best-of-content .boc__section .boc__sub-section .boc__sub-section-heading {
    border-bottom: 1px solid #c8cccc;
    color: #181715;
    color: #333;
    font-family: Work Sans, sans-serif;
    font-size: 15px;
    font-weight: 700;
    font-weight: 550;
    letter-spacing: -.5px;
    line-height: 22px;
    padding-bottom: 12px;
    text-transform: uppercase
}

.best-of-content .boc__section .boc__sub-section .boc__items {
    margin: 12px 0
}

.best-of-content .boc__section .boc__sub-section .boc__items .boc__item-list {
    margin-bottom: 12px
}

.best-of-content .boc__section .boc__sub-section .boc__items .boc__item-list .boc__item-link {
    color: #395bb6;
    font-family: Work Sans, sans-serif;
    font-size: 14px;
    letter-spacing: -.5px;
    line-height: 22px;
    text-decoration: none
}

.best-of-content hr {
    border: none;
    border-top: 1px solid #c8cccc;
    height: 1px;
    margin: 16px 0
}



.right-rail {
    -ms-flex: 0 0 100%;
    flex: 0 0 100%
}

@media screen and (min-width:600px) {
    .right-rail {
        min-width: 300px
    }
}

@media screen and (min-width:768px) {
    .right-rail {
        -ms-flex: 0 0 330px;
        flex: 0 0 330px;
        min-width: 330px
    }
}

@media screen and (min-width:1200px) {
    .right-rail {
        -ms-flex: 0 0 25%;
        flex: 0 0 25%;
        -ms-flex: 0;
        flex: 0;
        max-width: 300px;
        padding: 0
    }
}

.main-content-wrapper {
    width: 100%
}

@media (min-width:768px) {
    .main-content-wrapper {
        width: calc(100% - 330px)
    }
}

@media (max-width:768px) {
    .main-content-wrapper.full {
        width: 100%
    }
}

.source__container ul {
    font-family: Georgia, Arial, Helvetica, sans-serif
}

.mobile-ui .author-container .primary_author .author__block-image-wrap {
    display: none !important
}

.mobile-ui .author-container .primary_author .author__block-content {
    -ms-flex-align: baseline;
    align-items: baseline;
    -ms-flex-direction: row;
    flex-direction: row
}

.mobile-ui .article__content td p {
    font-size: 14px
}

@media (max-width:360px) {
    .mobile-advertiser-disclosure-wrapper {
        -ms-flex-direction: column;
        flex-direction: column;
        margin-top: 4px
    }

    .mobile-advertiser-disclosure-wrapper .mobile-date {
        margin-top: 0
    }
}

#footnote h2,
#footnote h3 {
    font-family: Merriweather, sans-serif, arial
}

#footnote ul {
    font-family: Work Sans, sans-serif
}

@media (min-width:768px) and (max-width:841px) {
    .right-rail-present .generic-products-carousel-wrapper .product-count-2 {
        text-align: left
    }
}


code {
    font-family: "JetBrains Mono", monospace;
    font-weight: 300;
}


pre[class*="language-"] {
    font-family: "Courier Prime", monospace;
    background: #ffffff;
}

/* Modify the styling for the boldtag element */
boldtag {
    font-weight: 100;
    font-size: small;
    font-family: "Krona One", sans-serif;
    font-size: inherit;
}

redtag {
    color: #00aced;
    margin-top: 1px;
    font-size: 18px;
    font-weight: bold;
    font-family: 'Kanit', sans-serif;
}

.container-maincontent p {
    margin-top: 10px;

}

.container-maincontent strong {
    font-size: medium;
    font-family: 'Roboto Mono';
}

.container-maincontent h4 {
    font-size: large;
    font-family: 'Montserrat';
    width: fit-content;
    margin-top: 20px;
}

code#code {
    font-size: 15px;
}

.chart {
    max-width: 100%;
    background: black;
    color: white;
    margin-top: 5px;
    border-radius: 10px;
}
body.dark-mode .chart{
    background: rgb(30 30 30);
}

.pro-cont {
    overflow-x: auto;
}

pre {
    padding: 10px;
    margin: 5px;
    width: fit-content;
    cursor: pointer;
    color: white;
    line-height: 1.6;
}

.featured-img-container img {
    margin-top: 10px;
    margin-bottom: 10px;
}

.lang-cont {
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* Distributes space between items */
    background-color: green;
}

.lang-cont p {
    margin: 0;
    font-family: monospace;
    /* Remove margin if needed */
}

.lang-cont svg {
    margin-left: auto;
    cursor: pointer;
    /* Pushes SVG to the right */
}

.article__content .container-maincontent li {
    list-style-type: disc;
    font-family: 'Poppins';
}