/* Global Styles */
body {
    background: #f5f5f5;
    margin-top: 20px;
    font-family: Arial, sans-serif;
}

/* Container */
.container-newpost {
    padding: 20px;
    background: white;
    border-radius: 8px;
    box-shadow: 0 1px 4px rgba(24, 28, 33, 0.12);
    max-width: 1200px;
    margin: 0 auto;
}

.container-newpost .font-weight-bold {
    font-weight: bold;
}

/* Card */
.card-newpost {
    border: 1px solid rgba(24, 28, 33, 0.03);
    border-radius: 8px;
    margin-bottom: 20px;
}

.card-newpost-body {
    padding: 20px;
}

.overflow-hidden {
    overflow: hidden;
}

/* Row Styles */
.row-newpost {
    display: flex;
    flex-wrap: wrap;
}

.row-newpost-bordered {
    border: 1px solid rgba(24, 28, 33, 0.1);
}

.row-newpost-border-light {
    border-color: rgba(24, 28, 33, 0.03);
}

/* Column */
.col-md-3 {
    width: 25%;
}

.col-md-9 {
    width: 75%;
}

/* List Group */
.list-group {
    padding-left: 0;
    margin-bottom: 0;
    border-radius: 8px;
}

.list-group-item {
    display: block;
    padding: 0.85rem 1.5rem;
    background-color: transparent;
    border: 1px solid rgba(24, 28, 33, 0.1);
    cursor: pointer;
}

.list-group-item-action:hover {
    background-color: rgba(24, 28, 33, 0.03);
}

.list-group-item.active {
    background-color: #26B4FF;
    color: white;
    border-color: #26B4FF;
    font-weight: bold;
}

/* Tabs */
.tab-content {
    padding: 20px;
}

.tab-pane {
    display: none;
}

.tab-pane.active {
    display: block;
}

/* File Input */
.account-settings-fileinput {
    position: absolute;
    visibility: hidden;
    width: 1px;
    height: 1px;
    opacity: 0;
}

.ui-w-80 {
    width: 80px;
    height: auto;
}

/* Buttons */
.btn {
    display: inline-block;
    padding: 8px 16px;
    font-size: 14px;
    border-radius: 4px;
    border: 1px solid transparent;
    cursor: pointer;
}

.btn-primary-newpost {
    background-color: #007bff;
    color: white;
    border-color: #007bff;
}

.btn-default {
    background-color: #f5f5f5;
    color: #4E5155;
    border-color: rgba(24, 28, 33, 0.1);
}

.btn-outline-primary {
    background-color: transparent;
    color: #26B4FF;
    border-color: #26B4FF;
}

.btn-facebook {
    background-color: #3B5998;
    color: white;
}

.btn-instagram {
    background-color: black;
    color: white;
}

/* Form Elements */
.form-group {
    margin-bottom: 20px;
}

.form-label {
    display: block;
    margin-bottom: 8px;
    font-weight: bold;
}

.form-control {
    display: block;
    width: 100%;
    padding: 8px;
    font-size: 14px;
    line-height: 1.5;
    color: #495057;
    background-color: white;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 4px;
}

textarea.form-control {
    resize: vertical;
}

.custom-select {
    display: block;
    width: 100%;
    padding: 8px;
    font-size: 14px;
    line-height: 1.5;
    color: #495057;
    background-color: white;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 4px;
}

/* Switcher */
.switcher {
    display: flex;
    align-items: center;
}

.switcher-input {
    margin-right: 10px;
}

.switcher-label {
    font-size: 14px;
}

.switcher-indicator {
    position: relative;
    width: 40px;
    height: 20px;
    background-color: #ced4da;
    border-radius: 12px;
    margin-right: 10px;
}

.switcher-yes,
.switcher-no {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.switcher-yes {
    left: 5px;
}

.switcher-no {
    right: 5px;
}

body.dark-mode .container-newpost {
    background: linear-gradient(135deg, #46484a 0%, #0c0d0d 100%);
    color: white;
}

body.dark-mode .container-newpost .form-group label {
    color: white;
}

body.dark-mode .container-newpost .form-group .form-control {
    color: white;
    background-color: black;
}

body.dark-mode .container-newpost .form-group .custom-select {
    color: white;
    background-color: black;
}


.card-newpost-body .toolbar {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
    gap: 10px;
    padding: 10px;
    border-radius: 8px;
}

.card-newpost-body .toolbar button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px;
    font-size: 14px;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.card-newpost-body .toolbar button:hover {
    background-color: #0056b3;
}

@media (max-width: 600px) {
    .card-newpost-body .toolbar {
        grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
    }
}

.info-para,
.optional-para {
    width: 100%;
    text-align: center;
    font-size: medium;
}

/* Basic setup for fade effect */
.tab-pane {
    opacity: 0;
    /* Start hidden */
    transition: opacity 0.2s ease-in-out;
    /* Transition effect */
}

/* When the tab-pane is active and should be visible */
.tab-pane.active.show {
    opacity: 1;
    /* Fully visible */
}

/* Optional: If you want the element to be hidden completely while fading in */
.tab-pane {
    display: none;
    /* Hide by default */
}

.tab-pane.active.show {
    display: block;
    /* Show when active */
}

@media (min-width: 768px) {
    .live-render .main-content-wrapper {
        width: 100%;
    }
}

.form-group .live-render-top-text {
    margin-top: -20px;
    margin-bottom: 20px;
    opacity: 0.8;
    width: 100%;
    text-align: center;
    font-weight: 200;
    font-family: 'JetBrains Mono';
}

.dynamic-container .form {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    width: 100%;
}

.dynamic-container form input {
    display: block;
    width: 100%;
    padding: 8px;
    font-size: 14px;
    line-height: 1.5;
    color: #495057;
    background-color: white;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 4px;
}

body.dark-mode .dynamic-container form input {
    color: white;
    background-color: black;
}

.dynamic-container form button {
    display: flex;
    align-items: center;
    gap: 5px;
    margin: 10px;
    width: fit-content;
    background-color: green;
    color: white;
    font-size: var(--fs-9);
    font-weight: var(--fw-600);
    padding: 8px 16px;
    border-radius: var(--radius-6);
}

.live-render .container-maincontent div {
    position: relative;
    /* Ensure the button is positioned relative to the div */
}

.live-render .container-maincontent div button {
    position: absolute;
    /* Position the button absolutely within the div */
    bottom: 10px;
    /* Adjust as needed */
    right: 10px;
    /* Adjust as needed */
    display: flex;
    align-items: center;
    gap: 5px;
    width: fit-content;
    background-color: green;
    color: white;
    font-size: var(--fs-9);
    font-weight: var(--fw-600);
    padding: 8px 16px;
    border-radius: var(--radius-6);
    opacity: 0;
    /* Initially hidden */
    transition: opacity 0.3s ease-in-out;
    /* Fade animation */
}

.live-render .container-maincontent div:hover button {
    opacity: 1;
    /* Show the button on hover */
}

@media (max-width:1022px) {
    .row-newpost {
        display: flex;
        flex-direction: column;
    }

    .col-md-3 {
        width: 100%;
    }

    .col-md-9 {
        width: 100%;
    }

    .list-group-flush {
        display: flex;
    }
    #main-content__wrapper{
        width: 100% !important;
    }
    .container-newpost {
        padding: 2px;
    }
    .container-newpost h4{
        margin-left: 15px;
        margin-top: 15px;
    }
    .card-newpost-body {
        padding: 1px;
    }
}