:root {

    /**
* colors
*/

    --selective-yellow: hsl(42, 94%, 55%);
    --eerie-black-1: hsl(0, 0%, 9%);
    --eerie-black-2: hsl(180, 3%, 7%);
    --quick-silver: hsl(0, 0%, 65%);
    --radical-red: hsl(351, 83%, 61%);
    --light-gray: hsl(0, 0%, 80%);
    --isabelline: hsl(36, 33%, 94%);
    --gray-x-11: hsl(0, 0%, 73%);
    --kappel_15: hsla(170, 75%, 41%, 0.15);
    --platinum: hsl(0, 0%, 90%);
    --gray-web: hsl(0, 0%, 50%);
    --black_80: hsla(0, 0%, 0%, 0.8);
    --white_50: hsla(0, 0%, 100%, 0.5);
    --black_50: hsla(0, 0%, 0%, 0.5);
    --black_30: hsla(0, 0%, 0%, 0.3);
    --kappel: hsl(170, 75%, 41%);
    --white: hsl(0, 0%, 100%);

    /**
* gradient color
*/

    --gradient: linear-gradient(-90deg, hsl(151, 58%, 46%) 0%, hsl(170, 75%, 41%) 100%);

    /**
* typography
*/

    --ff-league_spartan: 'League Spartan', sans-serif;
    --ff-poppins: 'Poppins', sans-serif;

    --fs-1: 4.2rem;
    --fs-2: 3.2rem;
    --fs-3: 2.3rem;
    --fs-4: 1.8rem;
    --fs-5: 1.5rem;
    --fs-6: 1.4rem;
    --fs-7: 1.3rem;

    --fw-500: 500;
    --fw-600: 600;

    /**
* spacing
*/

    --section-padding: 75px;

    /**
* shadow
*/

    --shadow-1: 0 6px 15px 0 hsla(0, 0%, 0%, 0.05);
    --shadow-2: 0 10px 30px hsla(0, 0%, 0%, 0.06);
    --shadow-3: 0 10px 50px 0 hsla(220, 53%, 22%, 0.1);

    /**
* radius
*/

    --radius-pill: 500px;
    --radius-circle: 50%;
    --radius-3: 3px;
    --radius-5: 5px;
    --radius-10: 10px;

    /**
* transition
*/

    --transition-1: 0.25s ease;
    --transition-2: 0.5s ease;
    --cubic-in: cubic-bezier(0.51, 0.03, 0.64, 0.28);
    --cubic-out: cubic-bezier(0.33, 0.85, 0.4, 0.96);

}





/*-----------------------------------*\
#RESET
\*-----------------------------------*/


li {
    list-style: none;
}



a {
    color: inherit;
    text-decoration: none;
}

img {
    height: auto;
}

input,
button {
    background: none;
    border: none;
    font: inherit;
}

button {
    cursor: pointer;
}




/*-----------------------------------*\
#REUSED STYLE
\*-----------------------------------*/


.shape {
    position: absolute;
    display: none;
}

.has-bg-image {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}



.section-title {
    --color: var(--radical-red);
    text-align: center;
}

.section-title .span {
    display: inline-block;
    color: var(--color);
}

.btn {
    background-color: var(--kappel);
    color: var(--white);
    font-family: var(--ff-league_spartan);
    font-size: small;
    display: flex;
    align-items: center;
    gap: 7px;
    max-width: max-content;
    padding: 10px 20px;
    border-radius: var(--radius-5);
    overflow: hidden;
}

.has-before,
.has-after {
    position: relative;
    z-index: 1;
}

.has-before::before,
.has-after::after {
    position: absolute;
    content: "";
}

.btn::before {
    inset: 0;
    background-image: var(--gradient);
    z-index: -1;
    border-radius: inherit;
    transform: translateX(-100%);
    transition: var(--transition-2);
}

.btn:is(:hover, :focus)::before {
    transform: translateX(0);
}

.img-holder {
    aspect-ratio: var(--width) / var(--height);
    background-color: var(--light-gray);
    overflow: hidden;
}

.img-cover {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.section-subtitle {
    font-size: var(--fs-5);
    text-transform: uppercase;
    font-weight: var(--fw-500);
    letter-spacing: 1px;
    text-align: center;
    margin-block-end: 15px;
}

.section-text {
    font-size: var(--fs-5);
    text-align: center;
    margin-block: 15px 25px;
}

.grid-list-footer {
    display: grid;
    gap: 30px;
}

.category-card,
.stats-card {
    background-color: hsla(var(--color), 0.1);
}

:is(.course, .blog) .section-title {
    margin-block-end: 40px;
}




/*-----------------------------------*\
#FOOTER
\*-----------------------------------*/

.footer {
    background-repeat: no-repeat;
    background-color: var(--eerie-black-2);
    color: var(--gray-x-11);
    font-size: medium;
    padding: 20px;
}

.footer-top {
    display: grid;
    padding-top: 30px;
    margin: 10px;
    gap: 30px;
}
.footer .techxcelerate{
    color: white;
}
.footer .techxcelerate h1{
    font-size: xx-large;
    font-family: "Protest Guerrilla";

}

.footer-brand-text {
    margin-block: 20px;
}

.footer-brand .wrapper {
    display: flex;
    gap: 5px;
}

.footer-brand .wrapper .span {
    font-weight: var(--fw-500);
}

.footer-link {
    transition: var(--transition-1);
}

.footer-link:is(:hover, :focus) {
    color: var(--kappel);
}

.footer-list-title {
    color: var(--white);
    font-family: var(--ff-league_spartan);
    font-size: xx-large;
    font-weight: var(--fw-600);
    margin-block-end: 10px;
}

.footer-list .footer-link {
    padding-block: 5px;
}

.newsletter-form {
    margin-block: 20px 35px;
}

.newsletter-form .input-field {
    background-color: var(--white);
    padding: 12px;
    border-radius: var(--radius-5);
    margin-block-end: 20px;
}

.newsletter-form .btn {
    min-width: 100%;
    justify-content: center;
}

.social-list {
    display: flex;
    gap: 25px;
}

.social-link {
    font-size: 20px;
}

.footer ul li {
    margin: 5px;
    padding: 1px;
}

.footer-bottom {
    border-block-start: 1px solid var(--eerie-black-1);
    padding-block: 30px;
}

.copyright {
    text-align: center;
}

.copyright-link {
    color: var(--kappel);
    display: inline-block;
}






/*-----------------------------------*\
#MEDIA QUERIES
\*-----------------------------------*/

/**
* responsive for large than 575px screen
*/

@media (min-width: 575px) {

    /**
* REUSED STYLE
*/

    .container-footer {
        max-width: 520px;
        width: 100%;
        margin-inline: auto;
    }

    .grid-list-footer {
        grid-template-columns: 1fr 1fr;
    }

    :is(.course, .blog) .grid-list-footer {
        grid-template-columns: 1fr;
    }


    /**
* FOOTER
*/

    .footer-brand,
    .footer-list:last-child {
        grid-column: 1 / 3;
    }

    .newsletter-form {
        display: flex;
        align-items: center;
        gap: 10px;
    }

    .newsletter-form .input-field {
        margin-block-end: 0;
    }

    .newsletter-form .btn {
        min-width: max-content;
    }

}





/**
* responsive for large than 768px screen
*/

@media (min-width: 768px) {

    /**
* CUSTOM PROPERTY
*/

    :root {

        /**
* typography
*/

        --fs-1: 4.6rem;
        --fs-2: 3.8rem;

    }



    /**
* REUSED STYLE
*/

    .container-footer {
        max-width: 720px;
    }

    .btn {
        padding: 15px 30px;
    }

    :is(.course, .blog) .grid-list-footer {
        grid-template-columns: 1fr 1fr;
    }



    /**
* FOOTER
*/

    .footer-brand,
    .footer-list:last-child {
        grid-column: auto;
    }

    .newsletter-form .btn {
        padding-block: 10px;
    }

}





/**
* responsive for large than 992px screen
*/

@media (min-width: 992px) {

    /**
* CUSTOM PROPERTY
*/

    :root {

        /**
* typography
*/

        --fs-1: 5.5rem;
        --fs-2: 4.5rem;

    }



    /**
* REUSED STYLE
*/

    .container-footer {
        max-width: 960px;
    }

    .grid-list-footer {
        grid-template-columns: repeat(4, 1fr);
    }

    :is(.course, .blog) .grid-list-footer {
        grid-template-columns: repeat(3, 1fr);
    }



    /**
* FOOTER
*/

    .footer .grid-list-footer {
        grid-template-columns: 1fr 0.6fr 0.6fr 1.2fr;
    }

}