/* Global Variables */
:root {
    --primary-color: #FF6F61;
    --secondary-color: #333;
    --text-color: #444;
    --heading-color: #222;
    --bg-color: #f9f9f9;
    --white: #ffffff;
    --transition-duration: 0.3s;
}

/* Container and Section */
.section {
    padding: 60px 0;
}

.container-home {
    max-width: 1200px;
    margin: 0 auto;
    padding-right: 7%;
}

/* Headings and Text */
.headline {
    color: var(--heading-color);
    margin-bottom: 10px;
}


.section-text {
    color: var(--text-color);
    font-size: 1.1rem;
    margin-bottom: 40px;
}

.grid-list-home {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
}

/* Recent Post Cards */
.recent-post-card {
    border-radius: 8px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform var(--transition-duration), box-shadow var(--transition-duration);
}

.recent-post-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 6px 30px rgba(0, 0, 0, 0.15);
}

/* Card Image */
.card-banner {
    width: 100%;
    height: 0;
    padding-bottom: calc(var(--height) / var(--width) * 100%);
    position: relative;
    overflow: hidden;
}

.card-banner img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* Card Content */
.card-content {
    padding: 20px;
}

.card-badge {
    display: inline-block;
    font-size: 0.9rem;
    color: var(--white);
    background-color: var(--primary-color);
    padding: 5px 10px;
    border-radius: 20px;
    margin-bottom: 10px;
    text-transform: uppercase;
}

.card-title {
    font-size: 1.4rem;
    color: var(--heading-color);
    margin-bottom: 15px;
}

.card-title .link {
    text-decoration: none;
    color: inherit;
    transition: color var(--transition-duration);
}

.card-title .link:hover {
    color: var(--primary-color);
}

.card-text {
    font-size: 0.8rem;
    color: var(--text-color);
    line-height: 1.6;
    margin-bottom: 20px;
}

/* Tags and Reading Time */
.card-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.card-tag a {
    color: var(--primary-color);
    font-size: 0.9rem;
    margin-right: 10px;
    text-decoration: none;
    transition: color var(--transition-duration);
}

.card-tag a:hover {
    color: var(--secondary-color);
}

.wrapper {
    display: flex;
    align-items: center;
    font-size: 0.9rem;
    color: var(--text-color);
}

.wrapper ion-icon {
    margin-right: 5px;
    font-size: 1.1rem;
}

/* Pagination */
.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
}

.pagination-btn {
    display: inline-block;
    padding: 10px 15px;
    margin: 0 5px;
    background-color: var(--white);
    border: 1px solid var(--text-color);
    border-radius: 5px;
    color: var(--text-color);
    text-decoration: none;
    transition: background-color var(--transition-duration), color var(--transition-duration);
}

.pagination-btn:hover {
    background-color: var(--primary-color);
    color: var(--white);
}

/* Responsive Design */
@media screen and (max-width: 768px) {

    .headline {
        text-align: left;
    }

    .section-text {
        text-align: center;
    }
}

/* Responsive Design */
@media screen and (min-width: 1117px) {


    .grid-list-home {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
        gap: 20px;
    }
}


body.dark-mode .card-title {
    color: white;
}

body.dark-mode .card-text {
    color: white;
}

body.dark-mode .wrapper {
    color: white;
}

.tags {
    padding: 60px 0;
    background-color: #ece5e5;
    border-radius: 10px;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.section-title-home {
    text-align: center;
    font-size: 2rem;
    color: #d51414;
    margin-bottom: 10px;
    font-family: 'Roboto', sans-serif;
}

.section-text {
    text-align: center;
    color: #777;
    font-size: 1.2rem;
    margin-bottom: 30px;
}

.grid-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.grid-list li {
    list-style: none;
}

.tag-btn {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    background-color: #fff;
    border: 2px solid #ddd;
    border-radius: 25px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.tag-btn:hover {
    border-color: #333;
    transform: translateY(-3px);
}

.tag-btn img {
    margin-right: 10px;
    border-radius: 50px;
}

.btn-text {
    font-size: 1rem;
    color: #333;
}

@media (max-width: 768px) {
    .grid-list {
        gap: 15px;
    }

    .tag-btn {
        padding: 8px 15px;
    }

    .btn-text {
        font-size: 0.9rem;
    }
}

/* Dark Mode */
body.dark-mode .tags {
    background-color: #1e1e1e;
}

body.dark-mode .section-text {
    color: #aaa;
}

body.dark-mode .tag-btn {
    background-color: #333;
    border-color: #555;
}

body.dark-mode .tag-btn:hover {
    border-color: #ddd;
}

body.dark-mode .btn-text {
    color: #f1f1f1;
}