p {
  font-family: 'Poppins';
}

.language-javascript {
  white-space: pre-wrap;
  /* Preserve whitespace and line breaks */
}


/* Change the background color of selected text to yellow */
::selection {
  background: greenyellow;
  /* Background color of selected text */
  color: black;
  /* Text color of selected text */
}

/* Change the scrollbar color to yellow */
::-webkit-scrollbar {
  width: 12px;
  /* Width of the scrollbar */
  height: 12px;
  /* Height of the scrollbar (for horizontal scrollbars) */
}

::-webkit-scrollbar-track {
  background: rgb(91, 87, 87);
  /* Track (part the scrollbar moves along) color */
}

::-webkit-scrollbar-thumb {
  background: rgb(49, 51, 47);
  /* Color of the draggable part of the scrollbar */
  border-radius: 1px;
  /* Optional: rounded corners for the scrollbar thumb */
}

::-webkit-scrollbar-thumb:hover {
  background: red;
  /* Color when hovering over the scrollbar thumb */
}
.firebase-check{
  background-color:hsl(180, 3%, 7%);
  text-align: center;
  color: #958b8b;
}
/* In your CSS file (e.g., App.css) */
.email-not-verified {
  /* Styles to apply when the email is not verified */
  background-color: #ff0015 !important;  /* Example background color */
  display: none;
}
