.sidebar {
    border-radius: 8px;
}

/* Post List Styles */
.post-list {
    margin-bottom: 20px;
    padding: 10px;
    background-color: #fff;
    border-radius: 8px;
    /* Separate box shadows for each side */
    box-shadow: 5px 0px 10px rgba(0, 0, 0, 0.04),
        /* Right shadow */
        -5px 0px 10px rgba(0, 0, 0, 0.04),
        /* Left shadow */
        0px 5px 10px rgba(0, 0, 0, 0.04),
        /* Bottom shadow */
        0px -5px 10px rgba(0, 0, 0, 0.04);
    /* Top shadow */
}
body.dark-mode .post-list{
    margin-bottom: 20px;
    padding: 10px;
    background-color: #2d2929;
    color: white;
    border-radius: 8px;
    /* Separate box shadows for each side */
    box-shadow: 5px 0px 10px rgba(0, 0, 0, 0.04),
        /* Right shadow */
        -5px 0px 10px rgba(0, 0, 0, 0.04),
        /* Left shadow */
        0px 5px 10px rgba(0, 0, 0, 0.04),
        /* Bottom shadow */
        0px -5px 10px rgba(0, 0, 0, 0.04);
}
body.dark-mode .post-list .post-info h3{
    color: rgb(222, 214, 214);
}
body.dark-mode .post-list .post-info span{
color: rgb(179, 172, 172);
}
body.dark-mode .sidebar  .sidebar-heading{
    color: white;
}
body.dark-mode .sidebar .social-media{
color: white;
background-color: #2d2929;
}

body.dark-mode  .best-of-content .boc__section{
    background-color: #2d2929;
    color: white;
}
body.dark-mode  .best-of-content .boc__section .boc__sub-section-heading{
    color: white;
}

.post-item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    cursor: pointer;
}

.post-image {
    width: 100px;
    margin-right: 15px;
    border-radius: 4px;
}

.post-info {
    flex: 1;
}

.post-info h3 {
    font-size: 15px;
    font-family: 'SUSE';
    margin-bottom: 5px;
    color: #333;
}

.post-info span {
    font-size: 13px;
    color: #777;
}

/* Social Media Styles */
.social-media {
    padding: 10px;
    background-color: #fff;
    border-radius: 8px;
    /* Separate box shadows for each side */
    box-shadow: 5px 0px 10px rgba(0, 0, 0, 0.04),
        /* Right shadow */
        -5px 0px 10px rgba(0, 0, 0, 0.04),
        /* Left shadow */
        0px 5px 10px rgba(0, 0, 0, 0.04),
        /* Bottom shadow */
        0px -5px 10px rgba(0, 0, 0, 0.04);
    /* Top shadow */
}

.sidebar-heading {
    font-size: 18px;
    color: #333;
    margin-bottom: 15px;
    position: relative;
    margin-left: 10px;
}

.sidebar-heading::before {
    content: '';
    position: absolute;
    left: -18px;
    top: 40%;
    width: 20px;
    height: 3px;
    background-color: red;
    transform: rotate(90deg);
}

.social-icons {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.social-icons a img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    padding: 5px;
    transition: transform 0.3s ease;
}

.social-icons a img:hover {
    transform: scale(1.1);
}

@media(max-width:775px) {
    .sidebar {
        margin-top: 20px;
    }
}
.sidebar-wrapper{
    margin-top: 20px;
}