/* General styles */
.courses-section {
    padding: 60px 0;
    background-color: #f5f5f5;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  /* Section title */
  .section-title-cources {
    font-size: 2.5rem;
    text-align: center;
    margin-bottom: 40px;
    text-align: center;
    margin-bottom: 50px;
    color: #d51414;;
    font-weight: bold;
  }
  
  /* Courses grid */
  .courses-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 30px;
  }
  
  /* Course card */
  .course-card {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    opacity: 0;
    transform: translateY(20px);
  }
  
  .course-card:hover {
    transform: translateY(-5px) rotate(0.5deg);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
    animation: buzzer 0.1s ease-in-out infinite alternate;
  }
  
  .course-card.animate-in {
    animation: fadeInUp 0.5s ease forwards;
  }
  
  /* Course image */
  .course-image-container {
    width: 100%;
    height: 200px;
    overflow: hidden;
  }
  
  .course-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
  }
  
  .course-card:hover .course-image {
    transform: scale(1.05);
  }
  
  /* Course title */
  .course-title {
    font-size: 1.25rem;
    margin: 0 0 10px;
    padding: 20px 20px 0;
    color: #333;
    font-weight: bold;
  }
  
  /* Course description */
  .course-description {
    font-size: 0.9rem;
    margin: 0 0 15px;
    padding: 0 20px;
    color: #666;
    line-height: 1.5;
  }
  
  /* Course details */
  .course-details {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    padding: 0 20px 20px;
    font-size: 0.8rem;
    color: #777;
  }
  
  .detail {
    display: flex;
    align-items: center;
    background-color: #f0f0f0;
    padding: 5px 10px;
    border-radius: 15px;
  }
  
  .detail i {
    margin-right: 5px;
  }
  
  /* Progress bar */
  .progress-bar-cources {
    height: 3px;
    background-color: #3498db;
    width: 0;
    transition: width 0.8s ease-out;
  }
  
  .course-card.animate-in .progress-bar-cources {
    width: 100%;
  }
  
  /* Animation keyframes */
  @keyframes fadeInUp {
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes buzzer {
    0% {
      transform: translateY(-5px) rotate(0.5deg);
    }
    100% {
      transform: translateY(-5px) rotate(-0.5deg);
    }
  }
  
  /* Responsive adjustments */
  @media (max-width: 768px) {
    .courses-grid {
      grid-template-columns: 1fr;
    }
    
    .section-title-cources {
      font-size: 2rem;
    }
  }
  
