*/
  
  /* Icon styles (you might want to use an icon font or SVGs instead) */
  .icon-clock::before { content: ''; }
  .icon-calendar::before { content: ''; }
  .icon-book::before { content: ''; }
  .icon-code::before { content: ''; }
  
  /* Additional styles for better visual appeal */
  .course-card {
    display: flex;
    flex-direction: column;
  }
  
/*
  .course-card > * {
    flex: 1 0 auto;
  }
*/
  
  .course-details {
    margin-top: auto;
  }
  
  .detail {
    transition: background-color 0.3s ease;
  }
  
  .detail:hover {
    background-color: #e0e0e0;
  }

  body.dark-mode .courses-section {
    color: white;
    background-color: black;
  }
  body.dark-mode .course-card {
    color: white;
    background-color: black;
  }
  body.dark-mode .detail {
    color: white;
    background-color: #2c2727;
  }
  body.dark-mode .course-title {
    color: white;
  }
  body.dark-mode .course-description  {
    color: white;
  }